.hx-section {
  border: 1px solid #eee;
  position: relative;
  margin-bottom: 16px;
  background-color: white;
  -webkit-box-shadow: 0 10px 15px -10px rgba(0, 0, 0, 0.1);
  box-shadow: 0 10px 15px -10px rgba(0, 0, 0, 0.1);
  overflow: hidden;
}
.hx-section header {
    top: 0;
    left: 0;
    width: 100%;
    font-size: 14px;
    line-height: 50px;
    height: 50px;
    padding: 0 16px 0 32px;
    color: #747474;
    position: absolute;
}
.hx-section header:before {
      content: "";
      height: 25px;
      width: 3px;
      background-color: #00B2EE;
      top: 50%;
      position: absolute !important;
      transform: translateY(-50%);
      -webkit-transform: translateY(-50%);
      -moz-transform: translateY(-50%);
      -o-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
      display: block;
      left: 16px;
}
.hx-section header small {
      font-size: 14px;
      color: #bbb;
      margin-left: 8px;
}
.hx-section header button {
      min-height: 34px;
      margin-left: 8px;
}
.hx-section header button.right {
        float: right;
        margin-top: 8px;
}
.hx-section .right {
    position: absolute;
    right: 16px;
    top: 8px;
}
.hx-section .right button {
      margin-left: 5px;
}
.hx-section .hx-divider {
    height: 1px;
    width: 100%;
    display: block;
    margin: 8px 0 0;
    padding: 0;
    background-color: #f8f8f8;
}
.hx-section.foldable header:after {
    content: "";
    height: 6px;
    width: 12px;
    opacity: .7;
    background: url(/static/img/icon-caret-down.ffe9537.png) no-repeat;
    background-size: 100% 100%;
    top: 50%;
    position: absolute !important;
    transform: translateY(-50%);
    -webkit-transform: translateY(-50%);
    -moz-transform: translateY(-50%);
    -o-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    -webkit-transform-origin: 50% 2px;
            transform-origin: 50% 2px;
    -webkit-transition: -webkit-transform .4s;
    transition: -webkit-transform .4s;
    transition: transform .4s;
    transition: transform .4s, -webkit-transform .4s;
    right: 24px;
    display: block;
}
.hx-section.foldable.fold {
    height: 50px !important;
    padding-top: 50px;
    padding-bottom: 0;
}
.hx-section.foldable.fold header:after {
      -webkit-transform: rotate(180deg);
              transform: rotate(180deg);
}
.hx-section.foldable .right {
    right: 48px;
}
