.hx-tooltip {
  position: relative;
  z-index: 10;
}
.hx-tooltip:before, .hx-tooltip:after {
    visibility: hidden;
    opacity: .0;
    transition: all 0.4s;
    -webkit-transition: all 0.4s;
    -moz-transition: all 0.4s;
    -o-transition: all 0.4s;
    -ms-transition: all 0.4s;
}
.hx-tooltip:hover {
    z-index: 11;
}
.hx-tooltip:hover:before, .hx-tooltip:hover:after {
      visibility: visible;
      opacity: 1.0;
}
.hx-tooltip:before {
    content: "";
    position: absolute;
    height: 0;
    width: 0;
    z-index: 999;
    transition: all 0.4s;
    -webkit-transition: all 0.4s;
    -moz-transition: all 0.4s;
    -o-transition: all 0.4s;
    -ms-transition: all 0.4s;
}
.hx-tooltip:after {
    content: attr(data-title);
    position: absolute;
    display: block;
    min-width: 135px;
    font-size: 12px;
    font-weight: 300;
    z-index: 9998;
    max-width: 200px;
    border: none;
    color: #f8f8f8;
    background-color: #474747;
    padding: 6px 8px;
    border-radius: 8px;
    -webkit-border-radius: 8px;
    -moz-border-radius: 8px;
    -o-border-radius: 8px;
    -ms-border-radius: 8px;
    -webkit-box-shadow: 0 6px 12px -4px rgba(0, 0, 0, 0.4);
    box-shadow: 0 6px 12px -4px rgba(0, 0, 0, 0.4);
}
.hx-tooltip.bottom:before, .hx-tooltip.bottom:after {
    top: 100%;
    left: 50%;
    transform: translateX(-50%);
    -webkit-transform: translateX(-50%);
    -moz-transform: translateX(-50%);
    -o-transform: translateX(-50%);
    -ms-transform: translateX(-50%);
}
.hx-tooltip.bottom:before {
    margin-top: 6px;
    border-bottom: 7px solid #474747;
    border-left: 7px solid transparent;
    border-right: 7px solid transparent;
}
.hx-tooltip.bottom:after {
    margin-top: 12px;
}
